import { gsap } from 'gsap';
import { ScrollSmoother } from 'gsap/ScrollSmoother';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { SplitText } from 'gsap/SplitText';
import { Observer } from 'gsap/Observer';

import { CustomEase } from 'gsap/CustomEase';
import { CustomBounce } from 'gsap/CustomBounce';
import { CustomWiggle } from 'gsap/CustomWiggle';
//* _____________  - ScrollTrigger, ScrollSmoother __________________
gsap.registerPlugin(ScrollTrigger, ScrollSmoother, SplitText, Observer,
	CustomEase, CustomBounce, CustomWiggle);

//* _____________________  - ScrollTrigger _________________________
ScrollTrigger.normalizeScroll(false);
ScrollTrigger.config({ ignoreMobileResize: true });

//* ----------------------------------------------------------------------------
const smoother = ScrollSmoother.create({
	wrapper: '#wrapper',
	content: '#content',
	speed: 1,
	smooth: 1,
	effects: true,
	smoothTouch: 1,
});

