export function initSectionTriggerMove(trigger, targets) {
	ScrollTrigger.create({
		trigger: trigger, //*  ,    -1   100px     
		start: 'top center',
		endTrigger: trigger, //*   -   /*      1     
		end: 'bottom center',
		toggleClass: { targets: targets, className: '_active' },
		// markers: true
	});
}
